.grid {
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 0px;
}
.message {
    padding-left: 10px;
    padding-top: 30px;
    color:indianred;
    text-align: left;
    font-size: medium;
    font-family:      Georgia, "Times New Roman", Times, serif;
    margin-bottom: 10px;
}